@import '../../../../data/scssFunctionFile/scssFunction.scss';

.ruleBookContent {
    width: 600px;
    height: 70vh;
    display: flex;
    flex-direction: column;
    position: relative;
    background-color: #ffffff;
    border-radius: 1rem;
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.25);
    padding: 1.5rem;
    max-width: 24rem;

    .ruleBookTitle {
        font-size: 1.5rem;
    }

    .indexlist {
        display: flex;
        flex-direction: column;
        font-size: 1.2rem;

        .indexbtn {
            all: unset;
            display: flex;
            align-items: center;
            padding: 10px;
            text-align: left;
            border-bottom: 1px solid #ccc;
            position: relative;
            padding-left: 0.5rem;

            .arrow {
                width: 0;
                height: 0;
                margin-right: 0.5rem;
                border-top: 6px solid transparent;
                border-bottom: 6px solid transparent;
                border-left: 10px solid #333;
            }

            &:last-child {
                border-bottom: none;
            }

            &:hover {
                color: $hoverTextcolor;
                cursor: pointer;
                background-color: $hovergreen;

                .arrow {
                    border-left-color: $hoverTextcolor;
                }
            }
        }
    }

    .ruleContent {
        .ruleBookTextTitle {
            font-size: 1.2rem;
        }
        .ruleBookTextSubTitle {
            display: inline-block;
            margin: 10px;
            font-size: 1.2rem;
            background: $bluemarker;
        }
        .ruleBookText {
            font-size: 1.1rem;
            font-weight: 600;
            line-height: 1.5rem;
            
            .redmarker {
                background: $redmarker;
            }

            .backcolor {
                padding: 2px 4px;
            }

            .redback {
                background-color: rgba(255, 0, 0, 0.7);
            }

            .yellowback {
                background-color: rgba(255, 255, 0, 0.7);
            }

            .thinRedback {
                background-color: $thinRed-color;
            }
            .thinYellowback {
                background-color: $thinYellow-color;
            }
            .thinGreenback {
                background-color: $thinGreen-color;
            }
            .thinBlueback {
                background-color: $thinBlue-color;
            }
        }

    }

    .userControle {

        .btn {

            &:hover {
                background-color: $btnhover-color;
            }
        }

        .closebtn {
            position: absolute;
            top: 20px;
            right: 20px;
        }

        .listbtn {
            position: absolute;
            top: 20px;
            right: 100px;
            padding: 5px 10px;
        }

        .pageControlebtn {
            position: absolute;
            bottom: 10px;
            left: 50%;
            transform: translateX(-50%);
            display: flex;
            justify-content: center;
            flex-direction: row;
            height: 35px;
            gap: 20px;
        }
        
        .backbtn,
        .nextbtn{
            width: 160px;
        }
    }
}